.displayNotes{
  position: relative;
  height: 60vh;
  width: 60vw;
  margin: 5vh 0;
  padding: 2.5vh;
  background: #e7e7e7;
  -webkit-box-shadow: 0 0 15px rgba(0,0,0,0.5);
          box-shadow: 0 0 15px rgba(0,0,0,0.5);
  left: 50%;
  -webkit-transform: translate( -50% , 0 );
      -ms-transform: translate( -50% , 0 );
          transform: translate( -50% , 0 );
}

@media screen and (max-device-width: 480px){
  .displayNotes{
    width: 90vw;
  }
}

.filterInput{
  border-radius: 1000px;
  padding: 0.5em 1em;
  border: none;
  -webkit-box-shadow: 0 0 5px rgba(0,0,0,0.4);
          box-shadow: 0 0 5px rgba(0,0,0,0.4);
  outline: none;
  margin: 2vh 2.5%;
  width: 50%;
}

.noteList{
  position: relative;
  height: 45vh;
  overflow: auto;
  padding: 0 2.5%;
}

.note{
  width: 100%;
  position: relative;
  height: auto;
  -webkit-box-shadow: 0 0 10px rgba(0,0,0,0.3);
          box-shadow: 0 0 10px rgba(0,0,0,0.3);
  padding: 1em;
  margin: 0.5em 0;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.note p:nth-child(2){
  -ms-flex:1 1 0%;
      flex:1 1 0%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  border: solid 1px red;
  height: 40px;
  min-width: 40px;
  max-width: 40px;
  border-radius: 50%;
  color: red;
  cursor: pointer;
}
.note p:first-child{
  -ms-flex: 10 1 0%;
      flex: 10 1 0%;
}



